import React from 'react';

import styles from "./ErrorBlock.module.scss"
import MainBtn from "../../layout/MainBtn/MainBtn";
import ImageWebp from "../../layout/ImageWebp/ImageWebp";
import {error404img, error404imgMob, error404imgMobWebp, error404imgWebp} from "../../../assets/images";
import {useNavigate} from "react-router-dom";

function ErrorBlock() {
    const navigate = useNavigate()
    const goBack = () => navigate(-1)

    return (
        <section className={`${styles['errorBlock']} container`}>
            <div className={styles["errorBlock__texts"]}>
                <h2 className={`${styles["errorBlock__title"]} headerTitle`}>Something went <span className="lightBlueText">wrong</span></h2>
                <p className={styles["errorBlock__contentText"]}>Error 404: Page not found.</p>
                <MainBtn onClick={goBack} className={styles['errorBlock__btn_desk']}>Go back</MainBtn>
            </div>
            <ImageWebp src={error404img} srcSet={error404imgWebp} alt="404" className={`${styles["errorBlock__img"]} ${styles["errorBlock__img_desk"]}`}/>
            <ImageWebp src={error404imgMob} srcSet={error404imgMobWebp} alt="404" className={`${styles["errorBlock__img"]} ${styles["errorBlock__img_mob"]}`}/>
            <MainBtn onClick={goBack} className={styles['errorBlock__btn_mob']}>Go back</MainBtn>
        </section>
    );
}

export default ErrorBlock;